// Variables for media queries
$media-large-desktop-width: 1400px;
$media-desktop-width: 992px;
$media-tablet-width: 768px;


// Color codes
$color-lightsteelblue: #BECCE2;
$color-darkgrey: #273142;
$color-darkslategray: #2f3f57;
$color-pitchblack: #1B2431;
$color-white: #fff;
$color-darkslateblue: #255a86;
$color-dodgerblue: #259AF2;


// Mixin for setting height and width 
@mixin dimension($width: auto, $height: auto) {
    width: $width;
    height: $height;
}

// Mixin for setting min/max width/height
@mixin minmaxDimension($min-width: 0, $max-width: none, $min-height: 0, $max-height: none) {
    min-width: $min-width;
    max-width: $max-width;
    min-height: $min-height;
    max-height: $max-height;
}
// Mixin for setting flex properties
@mixin flexbox($direction: row, $justify-content: flex-start, $align-items: stretch) {
    display: flex;
    flex-direction: $direction;
    justify-content: $justify-content;
    align-items: $align-items
}

// Mixin for setting element padding
@mixin padding($top: 0, $right: 0, $bottom: 0, $left: 0, $all: none) {
    @if $all == none {
        padding: $top $right $bottom $left;
    } @else {
        padding: $all;
    }
}

// Mixin for setting element margin
@mixin margin($top: 0, $right: 0, $bottom: 0, $left: 0, $all: none) {
    @if $all == none {
        margin: $top $right $bottom $left;
    } @else {
        margin: $all;
    }
}

// Mixin for setting border
@mixin border($type, $width, $style, $color) {
    @if $type == all {
        border: $width $style $color;
    } @else {
        border-#{$type}: $width $style $color;
    }
}

// Mixin for setting border-radius
@mixin border-radius($top-left: 0, $top-right: 0, $bottom-right: 0, $bottom-left: 0, $all: none) {
    @if $all == none {
        border-radius: $top-left $top-right $bottom-right $bottom-left;
    } @else {
        border-radius: $all;
    }
}

// Mixin for setting outline
@mixin outline($type, $width, $style, $color) {
    @if $type == none {
        outline: none;
    } @else {
        outline: $width $style $color;
    }
}

// Mixin for element position
@mixin position($type: relative, $top:auto, $right:auto, $bottom: auto, $left:auto) {
    position: $type;
    top: $top;
    right: $right;
    bottom: $bottom;
    left: $left;
}

// Mixin for fonts
@mixin font($font-style: normal, $font-variant: normal, $font-weight: normal, $font-size: medium, $font-family: 'Segoe UI') {
    font-style: $font-style;
    font-variant: $font-variant;
    font-weight: $font-weight;
    font-size: $font-size;
    font-family: $font-family;
}

// Mixin for text align center
@mixin textAlign($text-align: center) {
    text-align: $text-align
}

// Mixin for text overflow
@mixin overflow($type, $coord: both) {
    @if $coord == both {
        overflow: $type;
    } @else {
        overflow-#{$coord}: $type
    }
}

// Mixin for background color and font color
@mixin color($bg-color: inherit, $color: inherit) {
    color: $color;
    background-color: $bg-color;
}

//Mixin for css grids
@mixin parentPropsInCssGrid($grid-template-columns: none, $grid-template-rows: none, $grid-template-areas: none, $grid-column-gap: 0, $grid-row-gap: 0) {
    display: grid;
    grid-template-columns: $grid-template-columns;
    grid-template-rows: $grid-template-rows;
    grid-template-areas: $grid-template-areas;
    grid-column-gap: $grid-column-gap;
    grid-row-gap: $grid-row-gap;
}