@import './Common.scss';

html, body {
    @include dimension(100%, 100vh);
    @include margin($all:0);
    @include font($font-family: 'Segoe UI');
    @include color($bg-color:$color-pitchblack);
    overflow: hidden;
    #root {
        @include dimension($height:100%);
        @include minmaxDimension($min-width: 320px);
        @include flexbox($direction: column);

        #app-body-container {
            @include dimension($height:100vh);
            @include flexbox();
        }
    }
}