@import './Common.scss';

// CSS for all screen sizes
#header {
    @include dimension($height: 8%);
    @include minmaxDimension($min-height: 45px, $max-height: 75px);
    @include color($color-dodgerblue, $color-white);
    @include padding($all: 20px); 
    @include flexbox($align-items: center);

    span {
        @include padding(5px, 0, 5px, 10px); 
    }
}

@media(max-width: $media-tablet-width) {
    #header {
        img {
            height: 20px;
        }
        span {
            @include font($font-weight: 500, $font-size: 1em);
        }
    }
}

@media(min-width: $media-tablet-width) {
    #header {
        img {
            height: 30px;
        }
        span {
            @include font($font-weight: 400, $font-size: 1.2em);
        }
    }
}

