@import 'Common';

#solutions-container {
    @include color($color-darkgrey, $color-lightsteelblue);
    @include padding($all: 20px);
    @include flexbox($direction: column);

    &.hide {
        display: none;
    }
    
    span {
        @include margin($top: 5px, $bottom: 5px);
        opacity: 0.6;
    }

    .solutions {
        @include border(all, 1px, solid, $color-darkslategray);
        @include border-radius($all: 5px);
        @include margin($top: 15px, $bottom: 15px);
        @include padding($all: 5px);
        @include overflow($type: hidden);
        @include color($color-darkgrey, $color-lightsteelblue);
        text-overflow: ellipsis;
        cursor: pointer;
        opacity: 0.6;

        &.active {
            @include border(all, 1px, solid, $color-dodgerblue);
            outline: none;
            opacity: 1;
        }
    }
}

@media(max-width: $media-tablet-width) {
    #solutions-container {
        @include dimension(100%, 100%);
        @include font($font-size: 0.9em);
    }
}

@media(min-width: $media-tablet-width) {
    #solutions-container {
        @include dimension(20%, 100%);
        @include minmaxDimension($max-width: 325px);
        @include font($font-size: 0.95em);
    }
}