#questions-container {
    @include position($type: absolute);
    @include padding($all: 10px);
    @include dimension(100%, 100%);
    @include color($color-pitchblack, $color-white);
    overflow-y: auto;

    >div {
        .question {
            @include margin($top: 40px, $right: 0, $bottom: 40px, $left: 0);
            opacity: 0.6;

            &::before {
                content: attr(data-questioncount)".";
                @include position($type: absolute, $left: -30px);
            }

            &.active, &:hover  {
                outline: none;
                opacity: 1;
            }

            .pointer {
                cursor: pointer;
            }
        
            .option {
                @include margin($top: 12px, $right: 0, $bottom: 12px, $left: 0);
                @include border(all, 1px, solid, $color-darkslategray);
                @include font($font-size: 0.9em);
                @include border-radius($all: 5px);
                line-height: 0;

                &.active {
                    @include border(all, 1px, solid, $color-dodgerblue);
                    @include color($color-darkslateblue, $color-white);
                    outline: none;
                    opacity: 1;
                }

                label {
                    @include padding($all: 10px);
                    @include dimension($width: 100%);
                    @include margin($all: 0);
                              
                    .choice-container {
                        @include padding($left: 8px);
                        @include position($type: relative, $bottom: 2px);
                    }
                    
                }
            }
        }
    }
}


@media(max-width: $media-tablet-width) {
    #questions-container {
        >div {
            // Write css for width <=768px if required
        }
    }
}

@media(min-width: $media-tablet-width) {
    #questions-container {
        >div {
            @include position($type: absolute, $left: 50%);
            @include dimension($width: 500px);
            transform: translateX(-50%);
        }
    }
}