@import 'Common';

#architecture-container {
    @include dimension(100%, 100%);
    @include color($bg-color: #FFF);
    @include position($type: absolute);
     
    #diagram-wrapper {
        @include dimension(100%, 100%);
    }
}

@media(max-width: $media-tablet-width) {
    #architecture-container { 
        @include border(all, 10px, solid, $color-pitchblack);
    }
}

@media(min-width: $media-tablet-width) {
    #architecture-container { 
        @include border(all, 40px, solid, $color-pitchblack);
        &::after {
            content: '';
            position: absolute;
        }
    }
}
