@import './Common.scss';

#toggle-workspace {
    @include border(all, 1px, solid, $color-darkslategray);
    @include border-radius($all: 5px);
    @include position($type: absolute, $left: 50%, $top: 50%);
    opacity: 0.6;
    transform: translate(-50%, -50%);
    pointer-events: none;
    
    &.active {
        opacity: 1;
        pointer-events: all;
    }
    button {
        @include dimension(50%, 100%);
        @include color($color-pitchblack, $color-lightsteelblue);
        border: none;
        cursor: pointer;
        opacity: 0.6;

        &:nth-of-type(1) {
            @include border-radius($top-left: 5px, $bottom-left: 5px, $all: none)
        }

        &:nth-of-type(2) {
            @include border-radius($top-right: 5px, $bottom-right: 5px, $all: none)
        }
        &:focus {
            outline: none !important;
        }

        &.active {
            @include border(all, 1px, solid, $color-dodgerblue);
            @include color($color-darkslateblue, $color-white);
            outline: none;
            opacity: 1;
        }
    }
}

@media(max-width: $media-tablet-width) {
    #toggle-workspace {
        @include dimension(200px, 25px);
        @include font($font-size: 0.9em);
        line-height: 0;
    }
}

@media(min-width: $media-tablet-width) {
    #toggle-workspace {
        @include dimension(200px, 30px);
        @include font($font-size: 0.95em);
    }
}

