@import './Common.scss';

// CSS for all screen sizes
#portal-desc {
    @include flexbox($justify-content: flex-end);
    @include padding(10px, 20px, 10px, 20px);
    @include color($color-darkgrey, $color-lightsteelblue);
    @include position($type: relative);

    &.hide {
        display: none;
    }
    
    span {
        @include padding(10px, 0, 10px, 0);
    }
}

@media(max-width: $media-tablet-width) {
    #portal-desc {
        @include minmaxDimension($min-height: 35px);
        span {
            @include font($font-size: 0.9em);
        }
    }
}

@media(min-width: $media-tablet-width) {
    #portal-desc {
        @include border(top, 1px, solid, $color-darkslategray);
        @include border(bottom, 1px, solid, $color-darkslategray);
        img {
            cursor: pointer;
        }
    }
}

