@import 'common.scss';

#export-btn {
    @include color($bg-color: $color-dodgerblue);
    @include border-radius($all: 5px);
    @include margin($all: 10px);
    @include padding($all: 0);
    @include position($type: absolute, $right: 0);
    @include dimension(30px, 30px);
    outline: none;
    border: none;
    cursor: pointer;
}